<template>
	<view>
		<cu-custom bgColor="bg-gradual-blue" :isBack="true">
			<block slot="content">座位预约</block>
		</cu-custom>
		<!-- <homecom class="homclass"></homecom> -->
		<view class="">
			<form>
				<view class="cu-form-group margin-top">
					<view class="title">选择楼层</view>
					<picker @change="PickerChange" :value="index" :range="picker">
						<view class="picker">
							{{index>-1?picker[index]:'请选择'}}
						</view>
					</picker>
				</view>
				<view class="cu-form-group">
					<view class="title">开始时间</view>
					<picker mode="time" :value="form.starttime" start="09:00" end="22:00" @change="TimeChangestart">
						<view class="picker">
							{{form.starttime}}
						</view>
					</picker>
				</view>
				<view class="cu-form-group">
					<view class="title">结束时间</view>
					<picker mode="time" :value="form.endtime" start="09:00" end="22:00" @change="TimeChangeend">
						<view class="picker">
							{{form.endtime}}
						</view>
					</picker>
				</view>
				
			</form>
			<view class="margin-top submitbut">
				<button class="cu-btn block bg-blue margin-tb-sm lg"  @click="submit">提交</button>
			</view>

		</view>



	</view>
</template>

<script>
	import homecom from "@/components/homecom/homecom.vue"
	export default {
		mounted() {
			this.queryfloor()
		},
		components: {
			homecom
		},
		data() {
			return {
				form:{
					starttime: '09:01',
					endtime: '09:01',
				},
				index: -1,
				picker: [],
			}
		},
		methods: {
			submit(){
				let floortext = this.picker[this.index]
				let starttime = this.form.starttime
				let endtime = this.form.endtime
				let floor = this.index
				uni.navigateTo({
					url: `../seatchange/seatchange?floortext=${floortext}&starttime=${starttime}&endtime=${endtime}&floor=${floor}`
				});
			},
			async queryfloor() {
				let queryobj = {}
				let resdata = await new this.Request(this.Urls.m().floorquery, queryobj).modepost()
				console.log(resdata)
				resdata.forEach((val, i) => {
					this.picker.push(val.floordesc)
				})
			},
			PickerChange(e) {
				this.index = e.detail.value
			},
			TimeChangestart(e) {
				this.form.starttime = e.detail.value
			},
			TimeChangeend(e) {
				this.form.endtime = e.detail.value
			},

		}
	}
</script>

<style scoped>
	.submitbut{
		padding: 15px;
	}
</style>
